import React, { Component } from 'react';
import SoftwareCard from "../SoftwareCard/SoftwareCard.jsx";
import SoftwareLargeCard from "../SoftwareLargeCard/SoftwareLargeCard.jsx";
import { Redirect } from 'react-router-dom';
import "./AddSoftware.css";

class AddSoftware extends Component {
  constructor(props){
    super(props);
    console.log("Data from selection : ",this.props.location.state.data);
    this.state = {...this.props.location.state.data};
    this.state.selectedSoftareIndex = []
    this.state.microsoftSelected = false;
    this.state.renderAddAccessories = false;
    this.state.softwareArr = [
      {
        name : "7ZIP",
        image : "/assets/7zip-img.png",
        price : "Free"
      },
      {
        name : "Google Chrome",
        image : "/assets/chrome-img.png",
        price : "Free"
      },{
        name : "LibreOffice",
        image : "/assets/7zip-img.png",
        price : "Free"
      },{
        name : "Acrobat Reader",
        image : "/assets/7zip-img.png",
        price : "Free"
      },{
        name : "Audacity",
        image : "/assets/7zip-img.png",
        price : "Free"
      },{
        name : "Steam",
        image : "/assets/7zip-img.png",
        price : "Free"
      },{
        name : "GIMP",
        image : "/assets/7zip-img.png",
        price : "Free"
      },{
        name : "Handbrack",
        image : "/assets/7zip-img.png",
        price : "Free"
      } 
    ]
  }
  selectSoftware = (selectedItem) => {
      let softwareIndexArr = this.state.selectedSoftareIndex;
      let index = -1;
      softwareIndexArr.forEach((data, indexVal)=>{
          if(data == selectedItem){
            index = indexVal;
          }
      })
      if(index == -1){
        softwareIndexArr.push(selectedItem);
      }else{
        softwareIndexArr.splice(index,1)
      }
      this.setState({
        selectedSoftareIndex : softwareIndexArr
      },()=>{
        //Check the data updates
        console.log(this.state)
      })
  }
  microsoftSelect = () =>{
    if(this.state.microsoftSelected){
      this.setState({
        microsoftSelected : false
      })
    }else{
      this.setState({
        microsoftSelected : true
      })
    }
  }

  moveToAddAccessories = (finalPrice) =>{
    this.setState({
        finalPrice : finalPrice,
        renderAddAccessories : true
    })
  }

  renderAddAccessories = () => {
    if(this.state.renderAddAccessories){
      return (<Redirect to={{
                  pathname: '/add-accessories',
                  state: { data:  this.state }
              }} />)
    }
  }
  render() {
    let softwareList = [];
    let selected = false;
    this.state.softwareArr.forEach((data, index)=>{
      selected = false;
      if( this.state.selectedSoftareIndex.indexOf( index ) != -1 ){
        selected = true
      }
      softwareList.push(
          <div className="software-div">
            <SoftwareCard
              name = {data.name}
              imahe = {data.image}
              price = {data.price}
              index = {index}
              selected = {selected}
              click = {()=>this.selectSoftware(index)}
            />
          </div>
      )
    })
    let microsoftSelected = false;
  

    //Get selected software
    let selectedSoftwareIndex = [];
    let currSoftInex = ""
    let finalPrice =  parseFloat(this.state.totalPrice);
    this.state.selectedSoftareIndex.forEach((softIndex)=>{
      currSoftInex = {...this.state.softwareArr[softIndex]};
      if(currSoftInex.price == 'Free'){
        finalPrice += 0
      }else{
        finalPrice += currSoftInex.price
      }
      selectedSoftwareIndex.push(
            <div className="full-width">
                  <div className="col-sm-7">{currSoftInex.name}</div>
                  <div className="col-sm-4">Rs. {currSoftInex.price}</div>
            </div>
      )     
    })
    if(this.state.microsoftSelected){
      microsoftSelected = true;
      finalPrice += 4449.5
      selectedSoftwareIndex.push(
            <div className="full-width">
                  <div className="col-sm-7">{"Microsoft Home"}</div>
                  <div className="col-sm-4">Rs. {"4,449.5"}</div>
            </div>
      )   
    }
    return (
      <div className="accessories-main">
          {this.renderAddAccessories()}
          <div className="greet-header">
            Here's some softwares we think will help you more! 
          </div>
          <div className="greet-header">
            Just pick what you like and we'll install them for you, free of charge!
          </div>
          <div className="software-display">
            {softwareList}
          </div>
          <div className="pricing-div">
              <div className="software-lg-div full-height">
                  <SoftwareLargeCard
                    name = {"Microsoft Home and Student 2019, Lifetime Validity"}
                    price = "Rs 4999.95"
                    image = "/assets/microsoft-img.png"
                    selected = {microsoftSelected}
                    click = {this.microsoftSelect}
                  />
              </div>
              <div className="pricae-container-div full-height">
                  <div className="item-data-div">
                      <div className="full-width">
                            <div className="col-sm-7">Custom PC x 1</div>
                            <div className="col-sm-4">Rs. {this.state.totalPrice}</div>
                      </div>
                      {selectedSoftwareIndex}                
                  </div>
                  <div className="final-price-div">
                      <div className="full-width full-height">
                            <div className="col-sm-7">
                              <div className="final-price">Rs. {finalPrice}</div>
                              <div className="shipping-detail-div">Free shipping</div>
                            </div>
                            <div className="col-sm-4 full-height">
                                  <div onClick={()=> {this.moveToAddAccessories(finalPrice)}} className="next-button-style">
                                    Next
                                  </div>
                            </div>
                      </div>  
                  </div>
              </div>
          </div>
      </div>
    )
  }
}
export default AddSoftware;