import React, { Component } from 'react';
import { Image } from 'react-bootstrap';
import "./PlatformSelection.css";

class PlatformSelection extends Component {
  constructor(props){
      super(props);
      this.state = {
          processor : null
      }
  }
  updateProcessor = (name) => {
    this.setState({
        processor : name
    })
  }
  updateSelectionToMain = () => {
    if(this.state.processor == null){
        alert("Please select a processor")
    }else{
        this.props.onComplete(this.state)
    }
  }
  render() {
    const nextButtonStyle = {
        width: "100%",
        height : "100%",
        borderRadius : "25px",
        background : "#FFFFFF",
        textAlign : "center",
        fontSize : "2.2vw",
        fontWeight : "bold",
        paddingTop : "2%",
        color : "#455A64",
        cursor: "pointer",
    }
    let imageSize = {
        maxWidth:'100%',
        maxHeight:'100%',
    }
    let imageBoxIntel = {
        height : "50px",
        width : "100px",
        float : "left",
        background : "#0071C5",
        textAlign : "center",
        paddingTop : "0.5%",
        marginRight : "3%",
        cursor: "pointer",
    }
    let imageBoxAMD = {
        height : "50px",
        width : "100px",
        float : "left",
        background : "#017955",
        textAlign : "center",
        paddingTop : "0.5%",
        marginRight : "3%",
        cursor: "pointer",
    }
    if( this.state.processor == "intel" ){
        imageBoxIntel.border = "2px solid #FDD928";
        imageBoxAMD.border = "0px";
    }else if(this.state.processor == "amd"){
        imageBoxIntel.border = "0px";
        imageBoxAMD.border = "2px solid #FDD928"
    }
    return (
      <div className="platform-main">
         <div className="section-header">
            Step3: Choose your platform
         </div>
         <div className="platform-select-area">
             <div onClick={()=>{this.updateProcessor("intel")}}  style={imageBoxIntel}>
                <Image src="/assets/intel_icon.png" style={imageSize}/>
             </div>
             <div onClick={()=>{this.updateProcessor("amd")}}  style={imageBoxAMD}>
                <Image src="/assets/amd_icon.png" style={imageSize}/>
             </div>
         </div>
         <div className="footer-component">
              <div className="footer-start">

              </div>
              <div className="footer-details">

              </div>
              <div className="next-button">
                  <div onClick={this.updateSelectionToMain} className="select-next-button-style">
                    Next
                  </div>
              </div>
          </div>
      </div>
    )
  }
}
export default PlatformSelection;