import React, { Component } from 'react';
import CustomProgressBar from "../CustomProgressBar/CustomProgressBar.jsx";
import GameSelection from "../GameSelection/GameSelection.jsx";
import PlatformSelection from "../PlatformSelection/PlatformSelection.jsx";
import PriceSelector from "../PriceSelector/PriceSelector.jsx";
import { Redirect } from 'react-router-dom';
import Loader from 'react-loader-spinner'
import FormFactor from "./../FormFactor/FormFactor.jsx";
import Footer from "./../Footer/Footer.jsx"
import "./Selection.css";

const CONTENT = 0; 
const GAMING = 1; 
const SCIENTIFIC = 2; 

class Selection extends Component {
  constructor(props){
    super(props);
    console.log(this.props.pcSelect);
    this.state = {
      currentSelect : 0,
      redirectToBuild : false,
      priceSelected : 0,
      gamesArray : [],
      form : 2,
      platform : "intel",
      pcType : this.props.pcSelect,
    }
  }

  endOverlay = () => {
    if(this.state.overlay){
      this.setState({overlay:false})
      document.getElementById("overlay").style.display = "none";
    }
  } 

  initOverlay = (message) => {
      if(!this.state.overlay){
        this.setState({overlay:true})
        document.getElementById("loader-message").innerText = message
        document.getElementById("overlay").style.display = "block";
      }
  }

  nextSelection = (_State) => {

     if(this.state.pcType == CONTENT){
          if(this.state.currentSelect == 0){
            this.setState({
              priceSelected : _State.price, 
              currentSelect : 1
            })
          }else if(this.state.currentSelect == 1){
            this.setState({
              currentSelect : 2,
              platform : _State.processor
            })
          }else if(this.state.currentSelect == 2){
            this.setState({
              redirectToBuild : true,
              form : _State.form
            })
          }
     }else{
          if(this.state.currentSelect == 0){
            this.setState({
              priceSelected : _State.price, 
              currentSelect : 1
            })
          }else if(this.state.currentSelect == 1){
            this.setState({
              currentSelect : 2,
              platform : _State.processor
            })
          }else if(this.state.currentSelect == 2){
            this.setState({
              currentSelect : 3,
              gamesArray : _State
            })
          }else if(this.state.currentSelect == 3){
            this.setState({
              redirectToBuild : true,
              form : _State.form
            })
          }
      }
    }

    renderBuild = () => {
      if(this.state.redirectToBuild){
        return (<Redirect to={{
                    pathname: '/final-build',
                    state: { data:  this.state }
                }} />)
      }
    }

    render() {
    let progressTextStyle = {
        height : "100%",
        width : "25%",
        float : "left",
        color : "white",
        fontSize : "1.3vw",
        textAlign : "center"
    }
    let selectionComponent = "";
    let progressValue = 0;
    let stepName = "";

    if( this.state.pcType == CONTENT ){
      progressTextStyle.width = "33%";
      if(this.state.currentSelect == 0){
        progressValue = 33;
        selectionComponent = <PriceSelector onComplete={this.nextSelection} />
      }else if(this.state.currentSelect == 1){
        progressValue = 67;
        selectionComponent = <PlatformSelection onComplete={this.nextSelection} />
      }else if(this.state.currentSelect == 2){
        progressValue = 87;
        selectionComponent = <FormFactor onComplete={this.nextSelection} />
      }

      //Step details
      stepName =<div className="progress-text">
                  <div className="in-mobile-disappear"  style={progressTextStyle}>Step 1</div>
                  <div className="in-mobile-disappear"  style={progressTextStyle}>Step 2</div>
                  <div className="in-mobile-disappear" style={progressTextStyle}>Step 3</div>
                </div>
    }else{
      progressTextStyle.width = "25%";
      if(this.state.currentSelect == 0){
        progressValue = 20;
        selectionComponent = <PriceSelector onComplete={this.nextSelection} />
      }else if(this.state.currentSelect == 1){
        progressValue = 35;
        selectionComponent = <PlatformSelection onComplete={this.nextSelection} />
      }else if(this.state.currentSelect == 2){
        progressValue = 77;
        selectionComponent = <GameSelection pcType={this.state.pcType} onComplete={this.nextSelection} />
      }else if(this.state.currentSelect == 3){
        progressValue = 90;
        selectionComponent = <FormFactor onComplete={this.nextSelection} />
      }

      //Step details
      stepName =<div className="progress-text">
                  <div className="in-mobile-disappear" style={progressTextStyle}>Step 1</div>
                  <div className="in-mobile-disappear" style={progressTextStyle}>Step 2</div>
                  <div className="in-mobile-disappear" style={progressTextStyle}>Step 3</div>
                  <div className="in-mobile-disappear" style={progressTextStyle}>Step 4</div>
                </div>
    }
    return (
      <div className="platform-main">
         <div id="overlay">
          <Loader 
            className="loader-div"
            type="Puff"
            color="#00BFFF"
          />   
          <div id="loader-message" style={{color:'#FFFFFF',fontSize : '1.3vw'}}>Loading</div>
          </div>
        <div className="container">
            {this.renderBuild()}
            <div className="progress-area">
              {stepName}
              <CustomProgressBar
                progress = {progressValue}
              />
            </div>
            <div className="selection-component">
                {selectionComponent}
            </div>
        </div>
        {/* <div className="app-footer">
              <Footer/>    
        </div> */}
      </div>
    )
  }
}
export default Selection;