import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
//import { Slider } from '@material-ui/core';
import Slider from '@material-ui/core/Slider';
import Typography from '@material-ui/core/Typography';
import Tooltip from '@material-ui/core/Tooltip';
import "./PriceSlider.css";

const useStyles = makeStyles(theme => ({
  root: {
    width:  "100%" ,//1000 + theme.spacing(3) * 2,
    padding: theme.spacing(3),
  },
  margin: {
    height:  "100%" //theme.spacing(3),
  },
}));

function ValueLabelComponent(props) {
  const { children, open, value } = props;

  const popperRef = React.useRef(null);
  React.useEffect(() => {
    if (popperRef.current) {
      popperRef.current.update();
    }
  });

  return (
    <Tooltip
      PopperProps={{
        popperRef,
      }}
      open={open}
      enterTouchDelay={0}
      placement="top"
      title={value}
      style= {{display:"none"}}
    >
      {children}
    </Tooltip>
  );
}

ValueLabelComponent.propTypes = {
  children: PropTypes.element.isRequired,
  open: PropTypes.bool.isRequired,
  value: PropTypes.number.isRequired,
};

const PrettoSlider = withStyles({
  root: {
    color: '#52af77',
    height: 8,
  },
  thumb: {
    height: "100%",
    width: 30,
    backgroundColor: '#fff',
    border: '2px solid currentColor',
    marginTop: -8,
    marginLeft: -12,
    '&:focus,&:hover,&$active': {
      boxShadow: 'inherit',
    },
  },
  active: {},
  valueLabel: {
    left: 'calc(-50% + 8px)',
  },
  track: {
    height: 16,
    borderRadius: 8,
  },
  rail: {
    height: 16,
    borderRadius: 8,
  },
})(Slider);



class CustomizedSlider extends Component {
  constructor(props){
    super(props);

  }
  changePrice = (event) =>{
    let price = event.target.innerText;
    if( !isNaN(price) && price !== "" ){
      if( parseInt(event.target.innerText) > 500000 ){
        price = 500000;
      }
      let roundPriceValue = price;
      if( parseFloat(price) < 100000){
        roundPriceValue = (parseFloat(roundPriceValue)/1000).toFixed(1) + "K"
      }else{
        roundPriceValue = (parseFloat(roundPriceValue)/100000).toFixed(1) + " lakhs"
      }
      this.props.change({
        price : event.target.innerText,
        priceDisplay : "₹ " + roundPriceValue,
      })
    } 
  }

  showVal = (event) => {
    console.log(event)
  }

  roundNum = (num) => {
      console.log(   (parseFloat(num)/100000).toFixed(2) );
      return num
  }
  render(){
    const main = {
      height : "100%",
      paddingTop : "2%",
      paddingBottom : "2%",
      paddingLeft : "2%"
    }
    let price = this.props.priceDisplay;
    if(this.props.price >= 500000){
      price = "₹ 50K"
    }
    return (
      <Paper className="price-slider-main">
        <div  className="slider-div" >
          <PrettoSlider valueLabelDisplay="left" min={20000} max={500000} onChange ={this.changePrice} aria-label="pretto slider" defaultValue={50000} />  
        </div>
        <div className="data-div in-mobile-disappear">
          {price}
        </div>
      </Paper>
    );
  }

}
export default CustomizedSlider;