import React, { Component } from 'react'
import Typed from 'react-typed';
import { Redirect } from 'react-router-dom';
import Footer from "./../Footer/Footer.jsx"
import { Image } from 'react-bootstrap';
// import 'react-typed/dist/animatedCursor.css';
import './Home.css';


//Defining the price slider for the PC
let priceChanged = 50000;
const CONTENT = 0; 
const GAMING = 1; 
const SCIENTIFIC = 2; 
const DESKTOP = 1;
const MOBILE = 2;

class Home extends Component {
  constructor(props){
    super(props);

    const screenWidth = window.screen.width;
    let deviceType = DESKTOP;
    if( parseInt(screenWidth) <= 424 ){
      deviceType = MOBILE
    }else{
      deviceType = DESKTOP
    }

    this.state = {
       type : 0,
       fadeInterval : null,
       deviceType : deviceType,
       dynamicText : [
        'Or your money back',
        'To your next pc!',
        "On all our PC's'",
        "Pc configurator",
        "All over India.",
        "Guaranteed*",
        "In case pc is not working",
        "Always available for you.",
        "Before we ship the PC.",
        "In simulated hot/cold temps.",
        "On Credit cards.",
        "Coming soon",
       ],
       staticText : [
          'Experience the best PC performance',
          'Say hello',
          'Free WiFi + Bluetooth',
          "The world's most advanced",
          "Free delivery",
          "Your Windows will always be fast.",
          "Standby Component ",
          "24*7 Tech Support.",
          "70 Tests done",
          "PC tested",
          "Emi options available.",
          "Rent a Pc"
       ],
       selectedIndex : 0,
    }
  }  

  componentDidMount = () =>{
    var colors = ['#668C00B2', '#40BF0059', '#66D2000A','#405E35B1','#40FF8F00', '#402E7D32']; // Define Your colors here, can be html name of color, hex, rgb or anything what You can use in CSS
      var active = 0;
      const fadeInterval = setInterval(function(){
          document.querySelector('.home-main').style.background = colors[active];
          active++;
          if (active == colors.length) active = 0;
      }, 3000);
      this.setState({
        fadeInterval : fadeInterval
      })

      //fade the static text
      const staticInterval = setInterval(()=>{
          let index = this.state.selectedIndex;
          if( index < this.state.staticText.length - 1 ){
            index += 1;
          }else{
            index = 0
          }
          this.setState({
            selectedIndex : index
          })
      }, 5400);

      //this.getOS()
  }

  updateType = (num) => {
 
      const fadeInterval = this.state.fadeInterval;
      clearInterval(fadeInterval);
      this.setState({
        redirectToBuild : true,
      },()=>{
        this.props.onSelect({
          type : num
        })
      })
  }

  renderBuild = () => {
    if(this.state.redirectToBuild){
      return (<Redirect to={{
                  pathname: '/build',
                  state: { data:  this.state }
              }} />)
    }
  }


  getOS = () => {
      let userAgent = window.navigator.userAgent,
          platform = window.navigator.platform,
          macosPlatforms = ['Macintosh', 'MacIntel', 'MacPPC', 'Mac68K'],
          windowsPlatforms = ['Win32', 'Win64', 'Windows', 'WinCE'],
          iosPlatforms = ['iPhone', 'iPad', 'iPod'],
          os = null;
    
      if (macosPlatforms.indexOf(platform) !== -1) {
        os = 'Mac OS';
      } else if (iosPlatforms.indexOf(platform) !== -1) {
        os = 'iOS';
      } else if (windowsPlatforms.indexOf(platform) !== -1) {
        os = 'Windows';
      } else if (/Android/.test(userAgent)) {
        os = 'Android';
      } else if (!os && /Linux/.test(platform)) {
        os = 'Linux';
      }
      alert(os);
      //return os;
    
  }
  
  render() {
    let mText = this.state.staticText[ this.state.selectedIndex ];
    console.log(mText)
    console.log(this.state.staticText)
    console.log(this.state.selectedIndex)
    const imageSize = {
      width : "100%",
      height : "100%"
    }
    let playIcon = <span><Image src="/assets/play-icon.png" className="button-icon"/></span>
    let gameIcon = <span><Image src="/assets/games-icon.png" className="button-icon"/></span>
    let sciIcon = <span><Image src="/assets/scientific-ison.png" className="button-icon"/></span>
    
    if(this.state.deviceType == DESKTOP){
      playIcon = "";
      gameIcon = "";
      sciIcon = "";
    }

    return (
      <div className="home-main">
          <div className="container">
                {this.renderBuild()}
                <div className="space-div in-mobile-disappear"></div>
                <div className="static-text">{mText}</div>
                <div className="typed-text">
                    <Typed
                      strings={[
                           'Or your money back',
                           'To your next pc!',
                           "On all our PC's'",
                           "Pc configurator",
                           "All over India.",
                           "Guaranteed*",
                           "In case pc is not working",
                           "Always available for you.",
                           "Before we ship the PC.",
                           "In simulated hot/cold temps.",
                           "On Credit cards.",
                           "Coming soon"]}
                          typeSpeed={90}
                          backSpeed={90}
                          attr="placeholder"
                          loop >
                          <input className="typed-style" type="text"/>
                    </Typed>
                </div>
                {/* <div className="selection-area">
                  <div className="build-pc-btn">
                    Build a PC
                  </div>
                </div> */}
                <div className="selection-area">
                  <div className="full-height col-sm-2 in-mobile-disappear"></div>
                  <div className="full-height home-btn">
                      <div onClick={()=>{this.updateType(0)}} className="creator-box">
                          <div className="button-main-text">
                              <div className="col-sm-9 full-height">
                                CONTENT CREATION {playIcon}
                              </div>
                              <div className="col-sm-3 full-height in-mobile-disappear">
                                <Image src="/assets/play-icon.png" className="button-icon"/>
                              </div>
                          </div>
                          <div className="button-detail">
                            Adobe Premiere Pro / After Effects 
                            / Photoshop / LightRoom
                          </div>
                      </div>
                  </div>
                  <div className="full-height home-btn">
                      <div onClick={()=>{this.updateType(1)}}  className="gamer-box">
                        <div className="button-main-text">
                            <div className="col-sm-9 full-height">
                                GAMING { gameIcon}
                              </div>
                              <div className="col-sm-3 full-height in-mobile-disappear">
                                <Image src="/assets/games-icon.png" className="button-icon"/>
                              </div>
                        </div>
                        <div className="button-detail">
                          Optimized 
                          for Gaming/Streaming,Vr and more
                        </div>
                      </div>
                  </div>
                  <div className="full-height home-btn">
                      <div className="scientific-box">
                          <div className="button-main-text">
                                <div className="col-sm-9 full-height ">
                                    SCIENTIFIC {sciIcon}
                                  </div>
                                  <div className="col-sm-3 full-height in-mobile-disappear">
                                    <Image src="/assets/scientific-ison.png" className="button-icon"/>
                                </div>
                          </div>
                          <div className="button-detail">
                          Machine Learning,AI,
                          Computing and more  
                          </div>
                          <div className="comming-soon in-mobile-disappear">
                          Coming Soon...
                          </div>
                      </div>
                  </div>
                  <div className="full-height col-sm-3"></div>
          </div>
               
          <div className="space-div-bot"></div>
        </div>
        <div className="app-footer">
              <Footer/>    
        </div>
      </div>
    )
  }
}
export default Home;
