import React, { Component } from 'react';
import AccessoryCard from "./../AccessoryCard/AccessoryCard.jsx";
import { Redirect } from 'react-router-dom';
import "./AddAccessories.css"

class AddAccessories extends Component {

  constructor(props){
      super(props);
      this.state = {...this.props.location.state.data};
      this.state.selectedAccessories = []
      this.state.renderShipping = false;
      this.state.accessories = [
          {
              name : "Streamdeck",
              price : 13954,
              detail : "Made by Apple, this sleeve is crafted from high-quality European leather",
              index : 1
          },{
            name : "Streamdeck",
            price : 13954,
            detail : "Made by Apple, this sleeve is crafted from high-quality European leather",
            index : 2
        },{
            name : "Streamdeck",
            price : 13954,
            detail : "Made by Apple, this sleeve is crafted from high-quality European leather",
            index : 3
        },{
            name : "Streamdeck",
            price : 13954,
            detail : "Made by Apple, this sleeve is crafted from high-quality European leather",
            index : 4
        },{
            name : "Streamdeck",
            price : 13954,
            detail : "Made by Apple, this sleeve is crafted from high-quality European leather",
            index : 5
        },{
            name : "Streamdeck",
            price : 13954,
            detail : "Made by Apple, this sleeve is crafted from high-quality European leather",
            index : 6
        }
      ]
  }

  addAccessoriesIndex = (selectedItem) =>{
      let accessories = this.state.selectedAccessories;
      let index = -1;
      accessories.forEach((data, indexVal)=>{
          if(data == selectedItem){
            index = indexVal;
          }
      })
      if(index == -1){
        accessories.push(selectedItem);
      }else{
        accessories.splice(index,1)
      }
      
      this.setState({
        selectedAccessories : accessories
      })
  }

  moveToShipping = (finalPrice) =>{
    this.setState({
        finalPrice: finalPrice,
        renderShipping : true
    })
  }

  renderAddAccessories = () => {
    if(this.state.renderShipping){
      return (<Redirect to={{
                  pathname: '/shipping',
                  state: { data:  this.state }
              }} />)
    }
  }
  

  render() {
    const sampleDiv = {
        width : "100%",
        height : "60%",
    }
    
    let listAccessories = [];
    let selected = false;
    this.state.accessories.forEach((data, index)=>{
        selected = false;
        if(this.state.selectedAccessories.indexOf(index+1)!=-1){
            selected = true;
        }
        listAccessories.push(
            <div className="acc-card-div">
                <AccessoryCard
                    selected = {selected}
                    click = {()=>{this.addAccessoriesIndex(index+1)}}
                />
            </div>
        )
    })

    //Get selected software
    let selectedAccIndex = [];
    let currSoftInex = ""
    let finalPrice =  parseFloat(this.state.finalPrice);
    this.state.selectedAccessories.forEach((softIndex)=>{
      currSoftInex = {...this.state.accessories[softIndex]};
      if(currSoftInex.price == 'Free'){
        finalPrice += 0
      }else{
        finalPrice += currSoftInex.price
      }
      selectedAccIndex.push(
            <div className="full-width">
                  <div className="col-sm-7">{currSoftInex.name}</div>
                  <div className="col-sm-4">Rs. {currSoftInex.price}</div>
            </div>
      )     
    })
    return (
      <div className="accessories-main">
          {this.renderAddAccessories()}
          <div className="acc-header">
            Here's some <span className="bold-highlight">Accessories</span> we think will work <span className="italics-highlight"><i>perfectly</i></span> with your new desktop
          </div>
          <div className="accessories-data">
              <div className="col-sm-1 full-height in-mobile-disappear">

              </div>
              <div className="col-sm-7 full-height">
                    {listAccessories}
              </div>
              <div className="col-sm-4 price-div-height">
                    <div style={sampleDiv} className="in-mobile-disappear">

                    </div>
                    <div className="acc-pricing-div">
                    <div className="full-width full-height">
                            <div className="acc-item-data-div">
                                <div className="full-width">
                                        <div className="width-60">Custom PC x 1</div>
                                        <div className="width-40">Rs. {this.state.totalPrice}</div>
                                </div>
                                {selectedAccIndex}                 
                            </div>
                            <div className="acc-final-price-div">
                                <div className="full-width full-height">
                                    <div className="col-sm-7">
                                        <div className="final-price-acc">Rs. {finalPrice}</div>
                                        <div className="acc-shipping-detail">Free shipping</div>
                                    </div>
                                    <div className="col-sm-4 full-height">
                                            <div onClick={()=>{this.moveToShipping(finalPrice)}} className="acc-next-button-style">
                                                Next
                                            </div>
                                    </div>
                                </div>  
                            </div>
                        </div>
                    </div>
              </div>
          </div>
      </div>
    )
  }
}
export default AddAccessories;