import React, { Component } from 'react';
import { Image } from 'react-bootstrap';
import "./SoftwareLargeCard.css";

class SoftwareLargeCard extends Component {
  render() {
    let imageSize = {
        maxWidth:'100%',
        maxHeight:'100%',
    }
    let main = {
        height: "100%",
        width: "100%",
        textAlign: "start",
        cursor: "pointer",
        padding : "2%"
    }
    if(this.props.selected){
        main.border =  "2px solid #00e3c5"
    }
    return (
        <div onClick={this.props.click}  style={main}>
            <div className="software-lg-image-div">
                <Image src="/assets/microsoft-img.png" style={imageSize}/>
            </div>
            <div className="software-lg-name">
                {this.props.name}
            </div>
            <div className="software-lg-price">
                {this.props.price}
            </div>
        </div>
    )
  }
}
export default SoftwareLargeCard;