import React, { Component } from 'react';
import { Image } from 'react-bootstrap';

const DESKTOP = 1;
const MOBILE = 2;

class AccessoryCard extends Component {
  constructor(props){
    super(props);
    const screenWidth = window.screen.width;
    let deviceType = DESKTOP;
    if( parseInt(screenWidth) <= 424 ){
      deviceType = MOBILE
    }else{
      deviceType = DESKTOP
    }
    this.state = {
      deviceType : deviceType
    }
  }
  render() {
    let borderStyle = {
        width: "100%",
        height: "100%",
        border : "0.6px solid #eee",
        borderRadius : "7%",
        padding:"1%"
    }
    if(this.props.selected){
        borderStyle.border = "2px solid #00e3c5";
    }
   
    let imageDiv = {
        width : "100%",
        height : "50%",
        padding : "1.5%"
    }
    let imageSize = {
        maxWidth:'100%',
        maxHeight:'100%',
        background : "#000000"
    }
    let productName = {
        width: "100%",
        height: "10%",
        fontSize : "1.3vw",
        paddingLeft : "5%",
        paddingRight : "5%",
        color : "white",
        marginTop : "2%",
        fontWeight :"bold",
        textAlign : "start"
    }
    let productPrice = {
        width: "100%",
        height: "10%",
        fontSize : "0.8vw",
        paddingLeft : "5%",
        paddingRight : "5%",
        color : "white",
        textAlign : "start"
    }
    let productDetail = {
        width: "100%",
        height: "25%",
        fontSize : "0.8vw",
        paddingLeft : "5%",
        paddingRight : "5%",
        color : "white",
        textAlign : "start"
    }
    if(this.state.deviceType == MOBILE){
      productName.fontSize = "10px";
      productPrice.fontSize = "8px";
    }
    return (
      <div onClick={this.props.click} style={borderStyle}>
          <div style={imageDiv}>
            <Image src="/assets/streamdeck.png" style={imageSize}/>
          </div>
          <div style={productName}>
                Streamdeck
          </div>
          <div style={productPrice}>
                Rs. 13,954
          </div>
          <div style={productPrice}>
            Made by Apple,
            this sleeve is crafted from 
            high-quality European leather
          </div>
      </div>
    )
  }
}
export default AccessoryCard