  saveDataWithUniqueUrl = () => {
    const buildData = {...this.state};

    //Removing the arrat stored
    delete buildData['PCCaseArr'];
    delete buildData['bluetoothArr'];
    delete buildData['cpucoolerArr'];
    delete buildData['formFactorArr'];
    delete buildData['graphicCardArr'];
    delete buildData['motherboardArr'];
    delete buildData['peakPeakPowerArr']; 
    delete buildData['platformArr']; 
    delete buildData['processorArr'];
    delete buildData['ramFreqArr']; 
    delete buildData['ramSizeArr']; 
    delete buildData['smpsArr'];
    delete buildData['storageArr']; 
    delete buildData['storageSecondArr']; 
    delete buildData['wattageArr']; 
    delete buildData['wifiArr']; 

    //Now, once the data is clean. save the data;
    const dataStore = JSON.stringify(buildData);

    //Now, save the build and generate build number
    this.initOverlay("Generating your build...");
    agent.ORDER.saveBuild(dataStore)
      .then(response => {
          this.endOverlay();
          if(response.data.status == 1){
            let buildUrl = "http://localhost:3000/build-no/"
            //let buildUrl = "http://localhost:3000/build-no/"
            buildUrl = buildUrl + response.data.number;
            console.log("URL : " , buildUrl);
            alert(buildUrl)
          }else{
            this.alertDialog("Could not genearte the build number");
          }
    });

  }

  handleChangeReverse = (value) => {
    this.setState({
      reverseValue: value
    })
  }

  handleChange = (event, type)=>{
    let data = this.state.data;
    if(type == "name"){
      this.setState({
          name : event.target.value
      })
    }else if(type == "email"){
      this.setState({
        email : event.target.value
      })
    }else if(type == "mobile"){
      this.setState({
        mobile : event.target.value
      })
    }
  }

  changePlatform = (name) => {
      let filterDataSelection = this.state.filterDataSelection;
      if( this.state.filterDataSelection.platform == name ){
        
      }else{
        filterDataSelection.platform = name
        this.setState({
          filterDataSelection : filterDataSelection
        },()=>{
          console.log(this.state.filterDataSelection)
          this.initOverlay("Building the perfect PC for you!!!");
          this.fetchFilterPCData();
        })
      }
  }

  fetchFilterPCData = () => {
    agent.PCConfig.pcFilter(
        this.state.filterDataSelection.priceSelected,
        this.state.filterDataSelection.gamesArray,
        this.state.filterDataSelection.platform)
      .then(response => {
        console.log(response);
          this.endOverlay();
          if(response.data.status == 1){
            const pcData = response.data.result;
            console.log(pcData)
            this.setState({
              filterPCSelectedData : pcData,
              totalPrice : pcData.price
            })
          }else{
            this.setState({
              filterPCSelectedData : {}
            })
            this.errorDialog("No PC available with this configuration");
          }
      });
  }

