  updateConfigSelection = (type) => {
      if(type == 'WIFI'){
        if(this.state.wifiEnabled){
          this.setState({
            wifiEnabled : false
          })
        }else{
          this.setState({
            wifiEnabled : true
          })
        }
      }else if(type == 'BLUETOOTH'){
        if(this.state.bluetoothEnabled){
          this.setState({
            bluetoothEnabled : false
          })
        }else{
          this.setState({
            bluetoothEnabled : true
          })
        }
      }else if(type == 'MIC'){
        if(this.state.micEnabled){
          this.setState({
            micEnabled : false
          })
        }else{
          this.setState({
            micEnabled : true
          })
        }
      }else if(type == 'USBC'){
        if(this.state.defaultUSBC == true){
          this.errorDialog("USB-C support is inbuilt for this PC and cannot be removed.")
        }else{
          if(this.state.usbcEnabled){
            this.setState({
              usbcEnabled : false
            })
          }else{
            this.setState({
              usbcEnabled : true
            })
          }
        }
      }
  }

