import React, { Component } from 'react';
import { BrowserRouter as Router, Route ,Switch} from 'react-router-dom';
import CustomNavbar from './components/CustomNavbar/CustomNavbar.jsx';
import Home from './components/Home/Home.jsx';
import Selection from './components/Selection/Selection.jsx';
import FinalBuild from './components/FinalBuild/FinalBuild.jsx';
import AddSoftware from "./components/AddSoftware/AddSoftware.jsx";
import AddAccessories from "./components/AddAccessories/AddAccessories.jsx";
import FormFactor from "./components/FormFactor/FormFactor.jsx";
import Shipping from "./components/Shipping/Shipping.jsx";
import Transactions from "./components/Transactions/Transactions.jsx";
import Issues from "./components/Issues/Issues.jsx";
import UniqueUrl from "./components/UniqueURL/UniqueUrl.jsx";
import './App.css';

class App extends Component {

  constructor(props){
    super(props);
    this.state ={
      loginAccess : 1,
      pcType : 0,
    }
    if( window.location.pathname == "/" ){
      this.state.navbar = false
    }else{
      this.state.navbar = true
    }
  }

  moveToBuild = (_State) => {
    this.setState({
      navbar : true,
      pcType : _State.type
    })
  }
  backToHome = (_State) => {
    this.setState({
      navbar : false,
    })
  }

  openWebPage = (type) => {
    if(type == "facebook"){
      window.open("https://www.facebook.com/makemyrig/", "_blank");
    }else if(type == "twitter"){
      window.open("https://twitter.com/MakeMyRigHQ", "_blank");
    }else if(type == "instagram"){
      window.open("https://www.instagram.com/makemyrig/?hl=en", "_blank");
    }
  }
  

  render() {
    let pageSetup = "";
    let navbar = "", displayPage = "";
    if(this.state.navbar){
      document.getElementById("whatsapp-bubble").style.display = "none";
      navbar = <div className="navbar-section">
                  <CustomNavbar onHome={this.backToHome} logout = {()=>this.onLogout()}/>
               </div>
      displayPage = <div className="pages">
                    <Route 
                      exact path="/build" 
                      component = {() => <Selection pcSelect={this.state.pcType}/>} 
                    />
                    <Route exact path="/final-build" component = {FinalBuild} />
                    <Route exact path="/add-software" component = {AddSoftware} />
                    <Route exact path="/add-accessories" component = {AddAccessories} />
                    <Route exact path="/form-factor" component = {FormFactor} />
                    <Route exact path="/shipping" component = {Shipping} />
                    <Route exact path="/my-orders" component = {Transactions} />
                    <Route exact path="/queries" component = {Issues} />
                    <Route path="/build-no/:buildId" component={UniqueUrl}/>
                  </div>
    }else{
      document.getElementById("whatsapp-bubble").style.display = "block";
      navbar = "";
      displayPage = <div className="bg-pages">
                      <Route exact path="/" component = {() => <Home onSelect={this.moveToBuild}/>} />
                    </div>
    }
   if( this.state.loginAccess == 1){
      pageSetup = <div className="App">
                        {navbar}
                        {displayPage}
                        {/* <div className="app-footer">

                           
                        </div> */}
                    </div>
    }
    return (
      <div style={{ width : "100%", height : "100%" }} >
        <Router>
          {pageSetup}
        </Router>
      </div>
    );
  }
}

export default App;
