const Auth = {
	sendOtp : (mobile) => {
		return userRequests.post('send_otp/',
		 	qs.stringify({
				"mobile": mobile
			})
		 );
	},
	verifyOtp : (mobile,otp) => {
		return userRequests.post('verify_otp/',
		 	qs.stringify({
				"mobile": mobile,
				"otp":otp,
			})
		 );
	}
}

