import React, { Component } from 'react';
import GameBox from "./../GameBox/GameBox.jsx";
import Dialog from 'react-bootstrap-dialog'
import Loader from 'react-loader-spinner'
import agent from '../../services/agent.js';
import "./GameSelection.css";


const CONTENT = 0; 
const GAMING = 1; 
const SCIENTIFIC = 2; 

export default class GameSelection extends Component {

  constructor(props){
    super(props);
    this.state = {
        gameDataEntry : 0,
        gameAdded : "",
        pcType : this.props.pcType,
        selectedGameArray : [

        ],
        gameArray : [
            // {
            //     id : 1,
            //     name : "Game 1",
            //     image : "/assets/gm-1.png"
            // },{
            //     id : 2,
            //     name : "Game 1",
            //     image : "/assets/gm-1.png"
            // },{
            //     id : 3,
            //     name : "Game 1",
            //     image : "/assets/gm-1.png"
            // },{
            //     id : 4,
            //     name : "Game 1",
            //     image : "/assets/gm-1.png"
            // },{
            //     id : 5,
            //     name : "Game 1",
            //     image : "/assets/gm-1.png"
            // },{
            //     id : 6,
            //     name : "Game 1",
            //     image : "/assets/gm-1.png"
            // },{
            //     id : 7,
            //     name : "Game 1",
            //     image : "/assets/gm-1.png"
            // },{
            //     id : 8,
            //     name : "Game 1",
            //     image : "/assets/gm-1.png"
            // },{
            //     id : 9,
            //     name : "Game 1",
            //     image : "/assets/gm-1.png"
            // },{
            //     id : 10,
            //     name : "Game 1",
            //     image : "/assets/gm-1.png"
            // },{
            //     id : 11,
            //     name : "Game 1",
            //     image : "/assets/gm-1.png"
            // },{
            //     id : 12,
            //     name : "Game 1",
            //     image : "/assets/gm-1.png"
            // }
        ],
        softwareArray : [
            {
                id : 1,
                name : "Adobe Premirer pro",
                image : "/assets/acrobat-img.png"
            },{
                id : 2,
                name : "7Zip",
                image : "/assets/7zip-img.png"
            },{
                id : 3,
                name : "Adobe Photoshop",
                image : "/assets/7zip-img.png"
            },{
                id : 4,
                name : "Adobe Lightroom",
                image : "/assets/7zip-img.png"
            }
        ]
    }
  }
 
  componentDidMount =() => {
      this.initOverlay("Fetching the games data...")
      this.fetchAllGamesFromServer();
  }

  endOverlay = () => {
    if(this.state.overlay){
      this.setState({overlay:false})
      document.getElementById("overlay").style.display = "none";
    }
  } 

  initOverlay = (message) => {
      if(!this.state.overlay){
        this.setState({overlay:true})
        document.getElementById("loader-message").innerText = message
        document.getElementById("overlay").style.display = "block";
      }
  }

  fetchAllGamesFromServer = () => {
        agent.PCConfig.listGames()
        .then(response => {
            this.endOverlay();
            if(response.data.status == 1){
                //this.messageDialog("Fetched the game data");
                let gameArr = [];
                let gameObject = {};
                response.data.result.forEach((game)=>{
                    gameObject = {};
                    gameObject["id"] = game.pk
                    gameObject["name"] = game.fields.name
                    gameObject["fps"] = game.fields.fps
                    gameObject["image"] = game.fields.image
                    gameArr.push(gameObject)
                })
                this.setState({
                    gameArray : gameArr
                })
            }else{
                this.messageDialog("Could not update the details.");
            }
        });
  }

  selectGame = (data) =>{
    let selectedGame = this.state.selectedGameArray;
    let index = -1;
    selectedGame.forEach((game, indexValue) =>{
        if(game.id == data.id){
            index = indexValue
        }
    })
    if( index == -1 ){
        selectedGame.push(data)
        this.setState({
            selectedGameArray : selectedGame
        })
    }else{
        selectedGame.splice(index,1)
        this.setState({
            selectedGameArray : selectedGame
        })
    }
  }

  updateGameSelectionToMain = () => {
      if(this.state.selectedGameArray.length == 0 ){
          alert("Please select some game")
      }else{
          this.props.onComplete(this.state.selectedGameArray);
      }
  }

  onChangeText = (e) => {
      this.setState({
        gameAdded : e.target.value
      })
  }

  addGameToServer = (value) => {
    if(this.state.gameAdded == ""){
        alert("Please enter some name")
    }else{
        this.initOverlay("Adding details to out Database.")
        agent.PCConfig.gameAdd(this.state.gameAdded)
            .then(response => {
                this.endOverlay();
                if(response.data.status == 1){
                    this.messageDialog("It will been added soon.");
                    this.setState({
                        gameDataEntry : value
                    })
                }else{
                    this.messageDialog("Could not update the details.");
                }
            });
    }
  }

  updateGameDataEntry = (value) => {
    //When game to be added
    if( this.state.gameDataEntry == 1){
        this.addGameToServer(value)
    }else{
        this.setState({
            gameDataEntry : value
        })
    }
  }
  messageDialog = (message,type)=> {
    Dialog.setOptions({
      defaultOkLabel: 'Ok'
    })
    this.dialog.show({
      body: message,
      actions: [
        Dialog.OKAction(
          () => {
            
          }
        )
      ]
    })
  }
  render() {
    const nextButtonStyle = {
        width: "100%",
        height : "100%",
        borderRadius : "25px",
        background : "#FFFFFF",
        textAlign : "center",
        fontSize : "2.2vw",
        fontWeight : "bold",
        paddingTop : "2%",
        color : "#455A64",
        cursor: "pointer",
    }
    let selectStatement = "";
    let addStatement = "";

    let gameArrayDisplay = [];
    let selectedValue = false;
    if(this.state.pcType == CONTENT){
        selectStatement = "Choose the software you plan to use"
        addStatement = "Any other software?"
        this.state.softwareArray.forEach( (data) =>{
            selectedValue = false;
            this.state.selectedGameArray.forEach((input)=>{
                if( input.id == data.id ){
                    selectedValue = true
                }
            })
            gameArrayDisplay.push(
                <div key={data.id} className="col-xs-2 game-box">
                    <GameBox
                        id = {data.id}
                        name = {data.name}
                        image = {data.image}
                        selected = {selectedValue}
                        onSelect = {()=>this.selectGame(data)}
                    />
                </div>
            )
        })
    }else if( this.state.pcType == GAMING ){
        selectStatement = "Choose the game you plan to play"
        addStatement = "Any other game?"
        this.state.gameArray.forEach( (data) =>{
            selectedValue = false;
            this.state.selectedGameArray.forEach((input)=>{
                if( input.id == data.id ){
                    selectedValue = true
                }
            })
            gameArrayDisplay.push(
                <div key={data.id} className="game-box">
                    <GameBox
                        id = {data.id}
                        name = {data.name}
                        image = {data.image}
                        selected = {selectedValue}
                        onSelect = {()=>this.selectGame(data)}
                    />
                </div>
            )
        })
    }
    let moreGame = "";
    if(this.state.gameDataEntry == 0){
        moreGame =  <div onClick={()=>{this.updateGameDataEntry(1)}} className="question-text">
                        {addStatement}
                    </div>
    }else if(this.state.gameDataEntry == 1){
        moreGame =  <div className="data-entry">
                        <input className="data-entry-box" value={this.state.gameAdded} onChange={this.onChangeText} placeholder="Type the name of the game" type="text" />
                        <div onClick={()=>{this.updateGameDataEntry(2)}} className="send-btn">Send</div>
                    </div>
    }else if(this.state.gameDataEntry == 2){
        moreGame =  <div className="feedback">
                        Thanks for your feedback we will add it soon.
                    </div>
    }
    return (
      <div className="game-main">
          <div id="overlay">
                <Loader 
                    className="loader-div"
                    type="Puff"
                    color="#00BFFF" 
                />   
                <div id="loader-message" style={{color:'#FFFFFF',fontSize : '1.3vw'}}>Loading</div>
          </div>
          <Dialog ref={(el) => { this.dialog = el }} />
        <div className="section-header">
            Step2: {selectStatement}
        </div>
        <div className="game-select-area">
           {gameArrayDisplay}
        </div>
        <div className="footer-component">
              <div className="footer-start">

              </div>
              <div className="footer-details">
                {moreGame}
              </div>
              <div className="next-button">
                  <div onClick={this.updateGameSelectionToMain} className="select-next-button-style">
                    Next
                  </div>
              </div>
        </div>
      </div>
    )
  }
}
