import React, { Component } from 'react';
import "./Transactions.css";
import { Image } from 'react-bootstrap';
import {Table, Button, Modal} from 'react-bootstrap';
import Dialog from 'react-bootstrap-dialog'
import TextField from '@material-ui/core/TextField';
import Loader from 'react-loader-spinner'
import agent from '../../services/agent.js';
import TransactionCard from  "../TransactionCard/TransactionCard.jsx"


export default class Transactions extends Component {
    constructor(props){
        super(props);
        this.state = {
            userId : null,
            transactionsData : [],
            loginModel : false,
            loginModelSetForOTP : false,
            loadData : false,
            mobile : "",
            otp : ""
        }
    }

    componentDidMount = () => {
        if(this.state.userId == null){
           this.setState({
            loginModel : true
           })
        }
    }

    endOverlay = () => {
        if(this.state.overlay){
          this.setState({overlay:false})
          document.getElementById("overlay").style.display = "none";
        }
    } 

    initOverlay = (message) => {
        if(!this.state.overlay){
        this.setState({overlay:true})
        document.getElementById("loader-message").innerText = message
        document.getElementById("overlay").style.display = "block";
        }
    }

    alertDialog = (title,body) => {
        Dialog.setOptions({
            defaultOkLabel: 'Ok'
        })
        this.dialog.show({
            title: title,
            body: body,
            actions: [
            Dialog.OKAction(
                (el) => {

                }
            )
            ],
            bsSize: 'small',
            onHide: (dialog) => {
            dialog.hide()
            }
        })
    }

    verifySubmit = () => {
        if( this.state.mobile == undefined || this.state.mobile == ""){
              this.alertDialog("Incomplete Data", "Please provide the data")
        }else if(this.state.mobile.length !== 10){
              this.alertDialog("Invalid mobile number")
        }else{
          this.initOverlay("Your will receive an OTP shortly");
          this.setState({
            loginModel : false
          },()=>{
                agent.Auth.sendOtp(this.state.mobile)
                .then(data => {
                if( data.data.result.status == 'success'){
                    this.endOverlay();
                    this.setState({
                        loginModelSetForOTP : true,
                        loginModel : true
                    })
                }else{
                    //Show the message that could not verify the number
                    this.alertDialog("Could not send the OTP.");
                }
            });	
          })
          
        }
    }
    
      verifyOTP = () => {
          this.setState({
              loginModel : false
          },()=>{
            try{
                this.initOverlay("Verifying the OTP");
                    console.log();
                    agent.Auth.verifyOtp(this.state.mobile,this.state.otp)
                    .then(data => {
                        this.endOverlay();
                        console.log(data); 
                        if( data.data.status == 1){
                            this.setState({
                                mobileNumberVerified : true,
                                userId : data.data.user_id,
                                loginModel : false,
                                loginModelSetForOTP : false,
                                loadData : true
                            },()=>{ 
                                this.alertDialog("Mobile number verified.");
                            })
                        }else{
                            this.alertDialog("Mobile number could not be verified.");
                            this.setState({
                                loginModel : true,
                                loginModelSetForOTP : false
                            })
                        }
                    })
                    .catch(error=>{
                        this.alertDialog("Mobile number could not be verified.");
                        this.setState({
                            loginModel : true,
                            loginModelSetForOTP : false
                        })
                    })	
                }catch(e){
                    this.alertDialog("Mobile number could not be verified.");
                    this.setState({
                        loginModel : true,
                        loginModelSetForOTP : false
                    })
                }
          })
      }



    handleChange = (event, type)=>{
        let data = this.state.data;
        if(type == "mobile"){
            this.setState({
                mobile : event.target.value
            })
        }else if(type == "otp"){
            this.setState({
                otp : event.target.value
            })
        }
    }

    askToLoginForData = () => {
        const imageSize = {
            width : "50%",
            marginLeft : "25%",
            height : "100%"
        }
        let inputBox = "", submit = "", header = "";
        if(!this.state.loginModelSetForOTP){
            header = <div className="header_1">Enter your mobile number to proceed</div>
            inputBox = <input className="input-mobile" type="number" placeholder="Enter your mobile number" value={this.state.mobile} onChange={(e) => this.handleChange(e,'mobile')} />
            submit = <div onClick={this.verifySubmit} className="submit-button-transaction" >Submit</div>
        }else{
            header = <div className="header_1">Enter the OTP</div>
            inputBox = <input className="input-mobile" type="number" placeholder="Enter the OTP" value={this.state.otp} onChange={(e) => this.handleChange(e,'otp')} />
            submit = <div onClick={this.verifyOTP} className="submit-button-transaction" >Verify</div>
        }
        if(this.state.loginModel){
            return <Modal
                        show={this.state.loginModel}
                        // onHide={() => this.cloesLogin()}
                        dialogClassName="modal-90w"
                        aria-labelledby="example-custom-modal-styling-title" >
                            
                        <Modal.Header closeButton>
                        <Modal.Title id="example-custom-modal-styling-title">
                          <div style={{textAlign:"center", width : "100%"}}>Please log in to continue</div>
                        </Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            <div className="model-body">
                                <Image src="/assets/otp_process.svg" style={imageSize}/>
                                {header}
                                <div className="header_2">
                                    We require this verification in order to confim that you are a genuine user.
                                </div>
                                {inputBox}
                                {submit}
                            </div>
                        </Modal.Body>
                    </Modal>
          }
      }

  render() {
    let data = [];
    if(this.state.loadData == false){
        data = "";
    }else if(this.state.transactionsData.length == 0){
        data = <div className="no-transactions">You do not have any transactions.</div>
    }
    // else{
    //     for(let i = 0 ; i < 4 ; i++){
    //         data.push(
    //             <div className="transaction-card-div">
    //                 <TransactionCard/>
    //             </div>
    //         )
    //     } 
    // }
    return (
      <div className="transaction-main">
          {this.askToLoginForData()}
          <div id="overlay">
                <Loader 
                    className="loader-div"
                    type="Puff"
                    color="#00BFFF" 
                />   
                <div id="loader-message" style={{color:'#FFFFFF',fontSize : '1.3vw'}}>Loading</div>
          </div>
          <Dialog ref={(el) => { this.dialog = el }} />
          <div className="transaction-header">
              My Orders
          </div>
          <div className="transaction-area">
                {data}
          </div>
      </div>
    )
  }
}
