import React, { Component } from 'react';
import CustomizedSlider from "../PriceSlider/PriceSlider.jsx";
import "./PriceSelector.css";

class PriceSelector extends Component {
  constructor(props){
    super(props);
    this.state = {
      price : 50000,
      priceDisplay : "₹ 50000",
    }
  }
  changePrice = (_State) =>{
      this.setState({
        price : _State.price,
        priceDisplay : _State.priceDisplay
      })
  }
  updatePrice = () => {
    this.props.onComplete(this.state);
  }
  render() {
    return (
      <div className="price-main">
        <div className="section-header">
            Step1: Choose your price range
         </div>
         <div className="platform-select-area">
                <div className="price-slider-box">
                    <div className="slider-box">
                        <CustomizedSlider 
                          change = {this.changePrice}
                          price = {this.state.price}
                          priceDisplay = {this.state.priceDisplay}
                        />
                    </div>
                </div>
         </div>
         <div className="footer-component">
              <div className="footer-start">

              </div>
              <div className="footer-details">

              </div>
              <div className="next-button">
                  <div onClick={this.updatePrice} className="select-next-button-style">
                    Next
                  </div>
              </div>
          </div>
      </div>
    )
  }
}
export default PriceSelector;