import React, { Component } from 'react';
import { Image } from 'react-bootstrap';
import agent from '../../services/agent.js';
import "./GameBox.css";

class GameBox extends Component {
  render() {
    let imageSize = {
        maxWidth:'100%',
        maxHeight:'100%',
    }
    let tick = {
        height : "20px",
        width : "20px",
        borderRadius : "10px",
        background : "#FDD928",
        marginBottom : "-15px",
        marginLeft : "10px",
        zIndex: "10"
    }
    if(this.props.selected){
        imageSize.border =  "2px solid #FDD928"
    }
    const imageUrl = agent.URL.documentDownload + this.props.image;
    return (
      <div onClick={this.props.onSelect} className="game-box-main">
          {/* <div style={tick} ></div> */}
          <div className="game-image-div">
          
            <Image src={imageUrl} style={imageSize}/>
            {/* <Image src={this.props.image} style={imageSize}/> */}
          </div>
          <div className="game-name">
            {this.props.name}
          </div>
      </div>
    )
  }
}
export default GameBox;