import React, { Component } from 'react';
import "./CustomProgressBar.css";

class CustomProgressBars extends Component {
  render() {
    let fillerColor = "#9CFF38";
    let padding = "2px";
    let height = "15px";
    if( this.props.color != undefined ){
      fillerColor = this.props.color;
    }
    if( this.props.padding != undefined ){
      padding = this.props.padding +"px";
    }
    if( this.props.height != undefined ){
      height = this.props.height + "px";
    }
    let customprogressMain = { 
        width: "100%",
        height: height,
        borderRadius: "10px",
        background: "#FFFFFF",
        padding: padding,
    }
    let fillerStyle = {
        width:  this.props.progress +"%" ,
        height: "100%",
        borderRadius: "10px",
        background: fillerColor,
    }
    return (
      <div style={customprogressMain}>
          <div style={fillerStyle}>

          </div>
      </div>
    )
  }
}
export default CustomProgressBars;