import React, { Component } from 'react';
import { Image } from 'react-bootstrap';
import { Redirect } from 'react-router-dom';
import "./FormFactor.css";

class FormFactor extends Component {

  constructor(props){
      super(props);
      this.state = {
        selected : 2
      }
  }

  updateGameSelectionToMain = () => {
      this.props.onComplete({
          form : this.state.selected
      })
  }

  changeSelection = (num) => {
    this.setState({
        selected : num
    })
  }


//   renderAddAccessories = () => {
//     if(this.state.redirectToSoftwareSelect){
//       return (<Redirect to={{
//                   pathname: '/add-software',
//                   state: { data:  this.state }
//               }} />)
//     }
//   }

  render() {
    let roundSelectButton1 = {
        height : "30px",
        width : "30px",
        borderRadius : "50%",
        background : "#000000",
        border : "4px solid #FFFFFF",
        float : "left",
        marginTop : "5%",
        marginLeft : "15%",
        cursor : "pointer"
    }
    let roundSelectButton2 = {
        height : "30px",
        width : "30px",
        borderRadius : "50%",
        background : "#000000",
        border : "4px solid #FFFFFF",
        float : "left",
        marginTop : "5%",
        marginLeft : "15%",
        cursor : "pointer"
    }
    if(this.state.selected == 1){
        roundSelectButton1.background = "#00e3c5"
    }else{
        roundSelectButton2.background = "#00e3c5"
    }
    

    const nextButtonStyle = {
        width: "100%",
        height : "100%",
        borderRadius : "25px",
        background : "#FFFFFF",
        textAlign : "center",
        fontSize : "2.2vw",
        fontWeight : "bold",
        paddingTop : "2%",
        color : "#455A64",
        cursor: "pointer",
    }
    return (
      <div className="form-factor-main">
          <div className="section-header">
            Select your PC Form Factor
            </div>
            <div className="form-selection-div">
                <div className="form-data-entry">
                    <div className="form-image-div">
                        <Image src="/assets/form-1.png" className="image-size-form-1"/>
                    </div>
                    <div onClick={()=>{this.changeSelection(1)}} className="full-width">
                        <div style={roundSelectButton1}></div> 
                        <div className="select-text">Small Form Factor</div>
                    </div>
                </div>
                <div className="form-data-entry">
                    <div className="form-image-div">
                        <Image src="/assets/form-2.png" className="image-size"/>
                    </div>
                    <div onClick={()=>{this.changeSelection(2)}}  className="full-width">
                        <div style={roundSelectButton2}></div> 
                        <div className="select-text">Mid/Full Tower Form Factor</div>
                    </div>
                </div>
            </div>
            <div className="footer-component">
              <div className="footer-start">

              </div>
              <div className="footer-details">

              </div>
              <div className="next-button">
                  <div onClick={this.updateGameSelectionToMain} className="select-next-button-style">
                    Next
                  </div>
              </div>
          </div>
      </div>
    )
  }
}
export default FormFactor;